import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Form, Card, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip as RechartsTooltip, Legend } from 'recharts';
import logo from './logo.png'; // Import the logo image

function JobSearch() {
  const [selectedJob, setSelectedJob] = useState("");
  const [selectedEmployer, setSelectedEmployer] = useState("");
  const [employers, setEmployers] = useState([]);
  const [jobData, setJobData] = useState([]);
  const [jsonFiles, setJsonFiles] = useState([]);
  const [jobCounts, setJobCounts] = useState({});

  useEffect(() => {
    const loadJsonFiles = async () => {
      const fileUrls = ['2020.json', '2021.json', '2022.json'];
      const files = await Promise.all(fileUrls.map(async (url) => {
        const response = await fetch(url);
        const data = await response.json();
        return data;
      }));
      setJsonFiles(files.flat());
    };
    loadJsonFiles();
  }, []);

  const handleJobChange = (e) => {
    const jobTitle = e.target.value;
    setSelectedJob(jobTitle);
    setSelectedEmployer("");
    let filteredJobs = jsonFiles.filter(job => job['Job Title'].toLowerCase().includes(jobTitle.toLowerCase()));

    const distinctEmployers = [...new Set(filteredJobs.map(job => job.Employer))];
    setEmployers(distinctEmployers);
  };

  const handleEmployerChange = (e) => {
    setSelectedEmployer(e.target.value);
  };

  useEffect(() => {
    let filteredJobs = jsonFiles.filter(job => {
      const title = job['Job Title'].toLowerCase();
      const sector = job['Sector'].toLowerCase();
      const employer = job['Employer'];

      // Filter by selected employer if one is selected
      if (selectedEmployer && employer !== selectedEmployer) {
        return false;
      }

      const isInSector = sector === "colleges" || sector === "universities";
      if (!isInSector) {
        return false;
      }

      if (selectedJob === "Librarian") {
        return (
          title.includes("librarian") &&
          title !== "university librarian" &&
          title !== "associate university librarian" &&
          title !== "assistant chief librarian" &&
          title !== "associate chief librarian" &&
          title !== "chief librarian" &&
          title !== "associate dean / associate librarian"
        );
      } else if (selectedJob === "Associate University Librarian") {
        return (
          title === "associate university librarian" ||
          title === "assistant chief librarian" ||
          title === "associate chief librarian" || 
          title === "associate dean / associate librarian" || 
          title === "associate university librarian, academic" || 
          title === "associate director of library services"
        );
      } else if (selectedJob === "University Librarian") {
        return (
          title === "university librarian" ||
          title === "chief librarian"
        );
      } else {
        return title === selectedJob.toLowerCase();
      }
    });

    const jobCounts = {};
    const averages = ['2020', '2021', '2022'].map(year => {
      const jobsInYear = filteredJobs.filter(job => job.Year === year || job['Calendar Year'] === year);
      jobCounts[year] = jobsInYear.length;

      const totalSalary = jobsInYear.reduce((total, job) => {
        const salaryString = ['2020'].includes(year) ? job['Salary Paid'] : job.Salary;
        const salary = parseFloat(salaryString.replace(/[$,]/g, ''));
        return !isNaN(salary) ? total + salary : total;
      }, 0);

      const averageSalary = jobsInYear.length ? totalSalary / jobsInYear.length : 0;

      return { year, averageSalary };
    });

    setJobData(averages);
    setJobCounts(jobCounts);
  }, [selectedJob, selectedEmployer, jsonFiles]);


  return (
    <Container>
      <Row className="justify-content-md-center mt-4">
        <Col xs={12} md={6}>
          <div className="text-center">
            <img src={logo} alt="Logo" style={{ width: '200px' }} />
            <h1 className="mt-4 mb-4">Ontario Libraries in the Sunshine</h1>
            <OverlayTrigger
              placement="right"
              overlay={
                <Tooltip>
                  The way jobs are reported is not consistent, so it is difficult to ensure that everyone is categorized under the right heading. If anything, we've excluded some data points (so these numbers are approximate).
                </Tooltip>
              }
            >
              <Card.Text 
                className="text-center cta-description" 
                style={{ 
                  color: 'gray', 
                  fontSize: '1.2rem',
                  padding: '10px',
                  backgroundColor: '#f8f9fa', 
                  borderRadius: '10px' 
                }}
              >
                Discover the average salaries of various higher-ed library job roles in Ontario. To get started, select from one of the three categories below. Note that "Librarian" includes all ranks of non-administrative librarians.
              </Card.Text>
            </OverlayTrigger>
            <Form.Group>
              <Form.Label>Select a job</Form.Label>
              <Form.Control as="select" onChange={handleJobChange}>
                <option value="">Select a job</option>
                <option value="Librarian">Librarian</option>
                <option value="Associate University Librarian">Associate University Librarian</option>
                <option value="University Librarian">University Librarian</option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label>Select an employer</Form.Label>
              <Form.Control as="select" onChange={handleEmployerChange}>
                <option value="">Select an employer</option>
                {employers.map((employer, index) => (
                  <option value={employer} key={index}>{employer}</option>
                ))}
              </Form.Control>
            </Form.Group>
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          {jobData.map((data, index) => (
            <Card className="mb-4" key={index}>
              <Card.Body>
                <Card.Title>{data.year} Average Salary</Card.Title>
                <Card.Text>
                  <strong>Average Salary:</strong> {data.averageSalary.toFixed(2)}
                </Card.Text>
                <Card.Text>
                  <strong>Number of Jobs:</strong> {jobCounts[data.year]}
                </Card.Text>
              </Card.Body>
            </Card>
          ))}
        </Col>
        <Col>
          <LineChart width={500} height={300} data={jobData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="year" />
            <YAxis />
            <RechartsTooltip />
            <Legend />
            <Line type="monotone" dataKey="averageSalary" stroke="#8884d8" activeDot={{ r: 8 }} />
          </LineChart>
        </Col>
      </Row>
      <Row>
        <p>Data from <a href="https://www.ontario.ca/page/public-sector-salary-disclosure">over here.</a></p>
      </Row>
    </Container>
  );
}

export default JobSearch;
